.post {
    padding: 100px 0px;
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__img {
        max-width: 541px;
        img {
            width: 100%;
        }
    }
    &__title {
        margin-bottom: 40px;
        font-size: 45px;
        font-weight: 700;

    }
    &__content {
        max-width: 590px;
        display: flex;
        flex-direction: column;
    }
    &__text {
        margin-bottom: 40px;
        p {
        font-size: 20px;
        line-height: 29px;
        font-weight: 300;
        color: #000000;
        }
    }
    &__button {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}