*, h1, h2, h3, h4, h5, h6, ul {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Montserrat', sans-serif;
}
.container {
    max-width: 1300px;
    margin: 0 auto;
}
.button {
    max-width: 250px ;
    font-size: 12px;
    font-weight: 800;
    text-transform: uppercase;
    color: #ffffff;
    height: 58px;
    background-color: #0b0b0b;
    outline: none;
    border: none;
    cursor: pointer;
}
.title {
    font-size: 54px;
    font-weight: 700;
    color: #000000;
}