.main {
    height: 100vh;
    background: url('../img/svg/bg.jpg') center (center/cover) no-repeat;
    display: flex;
    justify-content: space-between;
    &__content {
        max-width: 700px;
        margin-top: 200px;
        display: flex;
        flex-direction: column;
        margin-left: 20px;
        margin-left: 70px;

    }
    &__img {
        max-width: 680px;
        img {
            width: 100%;
        } 
    }
    &__wrap {
        display: flex;
        justify-content: space-between;

    }
    &__title-small {
        font-size: 39px;
        font-weight: 800;
        text-transform: uppercase;
        color: #0b0b0b;
        margin-bottom: 30px;
    }
    &__title-big {
        font-size: 74px;
        line-height: 42px;
        font-weight: 800;
        text-transform: uppercase;
        color: #0b0b0b;
        margin-bottom: 30px;
    }
    &__title-descr {
        font-size: 20px;
        line-height: 29px;
        font-weight: 800;
        text-transform: uppercase;
        color: #0b0b0b;
        margin-bottom: 30px;
    }
}