.choose {
    display: flex;
    justify-content: space-between;
    &__anonce {
        font-size: 41px;
        font-weight: 700;
        text-transform: uppercase;
        color: #a2a2a2;
        margin-bottom: 20px;
    }
    &__img {
        max-width: 901px;
        img {
            width: 100%;
        }
    }
    &__title{
        margin-bottom: 35px;
    }
    &__wrap {
    display: flex;
    flex-direction: column;
    max-width: 500px;
    margin-left: 70px;
    }
    &__text{
        p {
        font-size: 20px;
        line-height: 33px;
        font-weight: 400;
        color: #3d3d3d;
        margin-bottom: 70px;
        }
    }
}