.footer {
    padding: 67px 0px;
    &__wrap {
        display: flex;
        flex-direction: column;
        background-color: #f3f3f3;
        padding: 60px 55px;
    }
    &__content-first {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 57px;
    }
    &__phone {
      display: flex;
      flex-direction: column;
      color: #fff;
      text-align: center;
    }
    &__call {
      font-size: 22.4px;
      line-height: 39px;
      font-weight: 800;
      text-transform: uppercase;
      background-color: #050505;
      margin-bottom: 2px;
      max-width: 140px;
    }
    &__number  {
        font-size: 32px;
        background-color: #050505;
        padding: 5px;
    } 
    &__social {
        ul {
            display: flex;
            list-style: none;
            li{
            margin-right: 12px;
            }
        }
    }
    &__navbar {
        ul {
            display: flex;
            list-style: none;
            li {
                margin-right: 55px;
                a {
                    text-decoration: none;
                    font-size: 16px;
                    font-weight: 700;
                    text-transform: uppercase;
                    color: #000000;
                }
            }
        }
    }
    &__content-second {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}