.forest {
    background: url('../img/svg/bg2.jpg') center (center/cover) no-repeat;
    padding: 90px 0px;
    &__title {
        color: #ffffff;
        text-align: center;
        margin-bottom: 35px;
        letter-spacing: 0.04em;
    } 
    &__subtitle {
        font-size: 20px;
        font-weight: 400;
        color: #ffffff;
        text-align: center; 
    }
    &__wrap {
        max-width: 700px;
        margin: 0 auto;
    }
}  