.header {
    padding: 37px 0px;
    width: 100%;
    z-index: 7;
    &__wrap {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__logo {
        margin-left: 20px;
        max-width:209px;
        img {
            width: 100%;
        }
    }
      &__menu {
        display: block;
        width: 50px;
        height: 50px;
        background-color: #050505;
        border-radius: 50%;
        position: relative;
        cursor: pointer;
      }
      &__menu span,
      &__menu span::before,
      &__menu span::after {
        position: absolute;
        top: 50%; margin-top: -1px;
        left: 50%; margin-left: -10px;
        width: 20px;
        height: 2px;
        background-color: #ffffff;
      }
      &__menu span::before,
      &__menu span::after {
        content: '';
        display: block;
        transition: 0.2s;
      }
      &__menu span::before {
        transform: translateY(-5px);
      }
      &__menu span::after {
        transform: translateY(5px);
      }
      
      &__menu-active span:before {
        transform: rotate(-45deg);
      }
      &__menu-active span:after {
        transform: rotate(45deg);
      }
      &__menu-active span {
      height: 0;
      }
    &__navbar {
        display: flex;
        align-items: center;
    }
    ul {
        display: flex;
        list-style: none;
        li {
            margin-right: 55px;
            a {
            font-size: 16px;
            font-weight: 900;
            text-transform: uppercase;
            color: #000000;
            text-decoration: none;
            }
        }
    }
}